<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>基本信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="page-list">
            <div class="pagelist-left">项目名称</div>
            <div class="pagelist-right">{{ jjxx.xmmc }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">供应商征集方式</div>
            <div class="pagelist-right">{{ jjxx.gysxzfs==='2'?'邀请':'公开' }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">联系人</div>
            <div class="pagelist-right">{{ jjxx.lxrmc }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">联系方式</div>
            <div class="pagelist-right">{{ jjxx.lxdh }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">联系邮箱</div>
            <div class="pagelist-right">{{ jjxx.email }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">备注</div>
            <div class="pagelist-right">{{ jjxx.bz }}</div>
          </div>
        </div>
      </div>

      <div v-if="noFile" class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>附件</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="page-list">
            <div class="pagelist-right">
              <XyUploadPlus
                ref="uploadEl"
                style="width:100%;"
                filed="test1"
                :is-show="false"
                :filegroup="jjxx.fj"
                @noFile="getFileState()"
              />
            </div>
          </div>
        </div>
      </div>

      <div v-if="jjxx" class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>竞价规则</h2>
            </div>
          </div>
          <!-- 通用标题 end -->

          <div class="page-list">
            <div class="pagelist-left">竞价时间</div>
            <div class="pagelist-right">{{ strToTime(jjxx.jjkssj) }} - {{ strToTime(jjxx.jjjssj) }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">竞价持续时间</div>
            <div class="pagelist-right">{{ getTime() }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">是否限制报价次数</div>
            <div class="pagelist-right">{{ jjxx.sfbjcs==='1'?'是':'否' }}</div>
          </div>
          <div v-if="jjxx.sfbjcs==='1'" class="page-list">
            <div class="pagelist-left">报价次数</div>
            <div class="pagelist-right">{{ jjxx.bjcs }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">是否允许延时竞价</div>
            <div class="pagelist-right">{{ jjxx.sfysjj ==='1'?'是':'否' }}</div>
          </div>
          <div v-if="jjxx.sfysjj==='1'" class="page-list">
            <div class="pagelist-left">延时竞价规则</div>
            <div v-if="jjxx.sfysjj==='1'" class="pagelist-right">离正式竞价结束时间前{{ jjxx.ysfptj }}分钟之内产生新的报价后,反拍结束时间自动延长{{ jjxx.zdycsj }}分钟</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">是否限制延时竞价次数</div>
            <div class="pagelist-right">{{ jjxx.sfxzyscs==='1'?'是':'否' }}</div>
          </div>
          <div v-if="jjxx.sfxzyscs==='1'" class="page-list">
            <div class="pagelist-left">延时次数</div>
            <div class="pagelist-right">{{ jjxx.ysfpcs }}</div>
          </div>

        </div>
      </div>

      <div v-if="jjxx.gysxzfs==='2'" class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>供应商信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->

          <div class="common-list">
            <div v-for="(item,index) of jjxx.gysxxList" :key="index" class="list">
              <h3><div class="tit">{{ item.gysmc }}</div></h3>
              <div class="info">
                <span><i class="iconfont iconlianxiren" />{{ item.lxr }}</span>
                <span><i class="iconfont iconshijian" />{{ item.lxfs }}</span>
                <span><i class="iconfont iconyoujian" />{{ item.email }}</span>
                <span><i class="iconfont iconzhengshubg" />{{ item.ztdm }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>出价步长设置</h2>
            </div>
          </div>
          <!-- 通用标题 end -->

          <div class="page-list">
            <div class="pagelist-left">出价步长设置方式</div>
            <div class="pagelist-right">{{ jjxx.djfs==='1'?'按绝对数量':'按百分比' }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">{{ jjxx.djfs==='1'?'按绝对数量':'按百分比' }}</div>
            <div class="pagelist-right">{{ jjxx.djslorbfb }}</div>
          </div>

        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <div class="title">
            <div class="title-left">
              <h2>商品信息</h2>
            </div>
          </div>
          <div class="common-list">
            <div v-for="(item,index) of jjxx.spxxList" :key="index" class="list">
              <div class="goodsshow-list">
                <div class="goodsshow-c">
                  <div class="goodsshow-img" @click="imgPreview(item.imgSrc?item.imgSrc:defaultImg)">
                    <img :src="item.imgSrc?item.imgSrc:defaultImg" :onerror="defaultImg">
                  </div>
                  <div class="goodsshow-c-info">
                    <div class="goodsshow-tit" @click="toProductDetail(item.id,item.xmmc)">商品名称：{{ item.xmmc }}</div>
                    <!-- <div class="goodsshow-tag">
                      <van-tag plain type="primary">京东商城</van-tag>
                    </div> -->
                    <div class="goodsshow-info">
                      <div class="price">单价：{{ item.dj }}元</div>
                      <div class="num">数量：{{ item.cgsl }}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large">我要参与</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { ImagePreview } from 'vant'
import { getDsjjxmxq, getProductInfo } from '@/store/module.js'
export default {
  name: 'BidMemberView',
  components: {
    headerBar,
    XyUploadPlus
  },
  data() {
    return {
      noFile: true,
      xmxh: '',
      headerData: {
        title: '竞价详情',
        leftUrl: 'back',
        leftText: ' '
      },
      jjxx: {},
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"'
    }
  },
  computed: {

  },
  created() {
    this.xmxh = this.$store.state.bid.xmxh
    this.getBidXq()
  },

  methods: {
    getFileState() {
      this.noFile = false
    },
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    toProductDetail(product_id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', product_id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    // 转换竞价持续时间
    getTime() {
      if (this.jjxx && this.jjxx.jjkssj && this.jjxx.jjjssj) {
        var jjkssj = new Date(this.jjxx.jjkssj.slice(0, 4), this.jjxx.jjkssj.slice(4, 6), this.jjxx.jjkssj.slice(6, 8), this.jjxx.jjkssj.slice(8, 10), this.jjxx.jjkssj.slice(10, 12), this.jjxx.jjkssj.slice(12, 14))
        var jjjssj = new Date(this.jjxx.jjjssj.slice(0, 4), this.jjxx.jjjssj.slice(4, 6), this.jjxx.jjjssj.slice(6, 8), this.jjxx.jjjssj.slice(8, 10), this.jjxx.jjjssj.slice(10, 12), this.jjxx.jjjssj.slice(12, 14))
        const second = (jjjssj - jjkssj) / 1000
        const day = Math.floor(second / 60 / 60 / 24)
        const houre = Math.floor(second / 60 / 60 - day * 24)
        const minute = Math.floor(second / 60 - (day * 24 + houre) * 60)
        const seconds = second % 60
        return day + '天' + houre + '时' + minute + '分' + seconds + '秒'
      }
    },
    strToTime(str) {
      if (str) {
        return str.slice(0, 4) + '-' + str.slice(4, 6) + '-' + str.slice(6, 8) + ' ' + str.slice(8, 10) + ':' + str.slice(10, 12)
      } else {
        return '  '
      }
    },
    getBidXq() {
      getDsjjxmxq.data = { xmxh: this.xmxh }
      this.asyncApiModule(getDsjjxmxq).then(res => {
        this.jjxx = res
        for (let i = 0; i < this.jjxx.spxxList.length; i++) {
          const spxx = this.jjxx.spxxList[i]
          getProductInfo.data = { e_product_id: spxx.id }
          this.asyncApiModule(getProductInfo).then(res => {
            spxx.imgSrc = res.e_product.picture
            this.jjxx.spxxList.splice(i, 1, spxx)
          })
        }
      })
    }
  }

}
</script>

<style lang="less" scoped>

</style>
